.guestHeader {
    display: flex;
    background-color: #d9d9d9;
    width: 100%;
    justify-content: space-between;
    padding: 0.6em;
    margin: 0.2rem;
  }
  .allBtn {
    background-color: #439a86;
    color: white;
    padding: 0.3em;
    border: 0px;
    font-size: 0.9em;
    border-radius: 5px;
    margin-left: 1em;
    min-width: 10rem;
  }
  .searchContainer{
    margin-left: 1em;
    padding: 0em 0.5em;
  }
  .viewBtn{
      margin-right:0;
  }
  @media screen and (max-width: 767px) {
    .allBtn {
      min-width: 5rem;
    }
    .viewBtn{
        display: none;
    }
    .searchContainer{
        width: 5rem;
    }
  }
  .flexEnd {
    margin: 0;
  }
  