.ratesHeader {
    display: flex;
    background-color: #d9d9d9;
    width: 100%;
    padding: 0.6em;
    height:3.5em;
    margin: 0.2rem;
}
.tabBox{
    padding:0.2em 0.8em;
    width: fit-content;
    height: 2em;
    display: block;
    margin:0.2em 0.7em;
    border: 1px solid lightgray;
    cursor: pointer;
    background-color: white;
}
.rateSettingHeader {
    font-weight: 700;
    font-size: 1.7em;
    padding-bottom: 0.3em;
    margin-top: 0.2em;
}
.taxHeader{
    font-weight: 700;
    font-size: 1.5em;
    padding-bottom: 0.3em;
    margin-top: 0.2em;
}
.rateBox{
    display: flex;
    flex-direction: row;
}
.weekdiscount{
    margin-left:1em;
}
/* .table{
    width: fit-content;
} */
.slider{
    cursor:pointer;
}
.allBtn {
    background-color: #439a86;
    color: white;
    padding: 0.3em;
    border: 0px;
    border-radius: 5px;
    min-width: 10rem;
    margin-right: 1em;
  }
  .rowSetup{
      margin-bottom: 0.7em;
  }
  .labelShow{
      font-weight: 560;
  }
@media (max-width: 600px) {
    .tabBox{
       font-size: 0.9em;
    }
    .rateSettingHeader{
        font-size: 1.3em;
    }
    .weekdiscount{
        margin-left: 0em;
    }
    .allBtn{
        min-width: 5rem;
    }
}

