p {
  font-family: sans-serif;
}

.header {
  display: flex;
  background-color: #d9d9d9;
  width: 100%;
  justify-content: space-between;
  padding: 0.6em;
  margin: 0.2rem;
}

.filter_container {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.filter_sub_container {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.filter_filter_name {
  display: block;
  width: 150px;
  margin-left: 10px;
  font-size: xx-large;
}

.filter_icon {
  padding-left: 0px;
}

.filter_title_header {
  font-size: x-large;
  margin: 5px;
}

.input_group {
  max-width: 500px;
}

.filter_title {
  background-color: transparent !important;
  border: none !important;
  width: 150px;
}

.filter_entry {
  display: block;
  border-radius: 5px !important;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  box-sizing: border-box;
  flex: 1 1 auto;
}

.btn_download {
  max-width: 300px;
}
.btn {
  background-color: #439a86;
  color: white;
  padding: 0.3em;
  border: 0px;
  font-size: 0.9em;
  border-radius: 5px;
  margin-left: 1em;
  margin-right: 3em;
  min-width: 10rem;
}

.btn:hover {
  background-color: #177e66;
}

.btn_group {
  display: flex;
  flex-direction: column;
}

.btn_csv {
  display: block;
  width: 150px;
  text-align: center;
}

.btn_csv:hover {
  text-decoration: none;
  background-color: #177e66;
  color: white;
}

@media (min-width: 1700px) {
  .filter_container {
    flex-direction: row;
  }

  .input_group {
    max-width: 300px !important;
  }
}

@media (min-width: 992px) {
  .filter_sub_container {
    flex-direction: row;
  }

  .input_group {
    max-width: 350px;
  }

  .filter_title {
    width: auto;
  }
}

@media (min-width: 576px) {
  .btn_group {
    display: flex;
    flex-direction: row;
  }
}
