.borderBox{
    display: block;
    width: fit-content;
    border: 1px solid lightgray;
    margin-right: 1em;
    padding: 0.5rem;
    background-color: #fff;
}
.mainContainer{
    display: flex;
    flex-direction: row;
}
.scroll_menu_arrow {
    cursor: pointer;
    padding:0em 0.5em ;
    height: fit-content;
    font-size: 0.8rem;
    align-self: center;
}
.middleSelection{
    width: fit-content;
    text-align: center;
    padding:0em 0.3em;
    min-width: 5rem;
}