.blockdate{
  background-color: lightgray;
  text-align: center;
}
.rctItemContent {
  padding: 0 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.itemText {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.itemStatus{
  padding: 7px;
  border-radius: 50%;
}
.itemNoStatus{
  padding: 5px;
  border: 2px solid;
  border-radius: 50%;
}
.customGroup {
  position: relative;
}
.customGroup:after {
  content: '';
  border-left: 10px solid transparent;
  border-bottom: 10px solid #333;
  position: absolute;
  right: 0;
  bottom: 0;
}

.groupColourRed:after {
  border-bottom: 10px solid red;
}

.groupColourYellow:after {
  border-bottom: 10px solid yellow;
}

.groupColourGreen:after {
  border-bottom: 10px solid springgreen;
}

.groupColourBlack:after {
  border-bottom: 10px solid #000;
}

.header {
  background-color: white;
}

.dataHeader {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  border-bottom: 1px solid #000;
  cursor: pointer;
  font-size: 14px;
  background-color: #ffffff;
  border-left: 1px solid #000;
}