.rentalHeader {
  display: flex;
  background-color: #d9d9d9;
  width: 100%;
  justify-content: space-between;
  padding: 0.6em;
  margin-bottom: 0.1rem;
  margin-top: 0.1rem;
}
.iconstyle {
  height: 40px;
  width: auto;
  padding: 6px;
  align-items: center;
}
.allBtn {
  background-color: #439a86;
  color: white;
  padding: 0.3em;
  border: 0px;
  font-size: 0.9em;
  border-radius: 5px;
  min-width: 10rem;
}
.icons{ 
  cursor: pointer;
}
.selectedIcon {
  background: #a7a6a6;
}

@media screen and (max-width: 767px) {
  .allBtn {
    min-width: 7rem;
  }
}
.flexEnd {
  margin: 0;
}

.modalFooter{
  margin: 0.7em 0em;
  align-items: center;
  text-align: center;
  padding: 0.5em;
}