.logoHeader {
  width: auto;
  height: 1.7em;
}
.topBarPanel {
  box-shadow: 0px 1px 3px black;
  background-color: #ffffff !important;
}
.linkcontainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}
.imgStyle {
  height: 40px;
  width: auto;
  padding: 6px;
  align-items: center;
}
.topLink {
  text-decoration: none;
  color: #000;
}
.topLink .topLinkText {
  text-decoration: none;
}
.dropdownBox {
  margin: 0;
  display: flex;
}
.hiddenLink {
  display: none;
}
.dropdownStyle {
  width: min-content;
  margin-right: 35px;
}
.dateSetup{
    position: absolute;
    margin-left:50rem;
    font-size: 0.9em;
    padding: 1em;
    z-index: 200;
    display: none;
}
@media (max-width: 800px) {
    .imgStyle{
        display: none;
    }
    .linkcontainer{
       align-items: flex-end;
       margin-left: 0;
       margin: 0.2em;
    }
    .dropdownBox{
       display: none;
    }
    .hiddenLink{
        margin: 0.2em;
        display: block;
       text-align: right;
    }
    .dateSetup{
        margin-left: 12rem;
        font-size: 0.8em;
        position: fixed;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
.isActive {
  background: gainsboro;
}

.header_sm_menu {
  width: calc(100vw - 110px);
}