.calendarHeader {
    display: flex;
    background-color: #d9d9d9;
    width: 100%;
    justify-content: space-between;
    padding: 0.6em;
    margin: 0.1rem;
  }
  .iconstyle {
    height: 40px;
    width: auto;
    padding: 6px;
    align-items: center;
  }
  .allBtn {
    background-color: #439a86;
    color: white;
    padding: 0.3em;
    border: 0px;
    font-size: 0.9em;
    border-radius: 5px;
    margin-left: 1em;
    min-width: 10rem;
  }
  .flexEnd {
    margin: 0;
  }
  .menu_item {
    padding: 0 1em;
    margin: 5px;
    user-select: none;
    cursor: pointer;
    border: none;
  }
  .menu_item_wrapper.active {
    border: 1px blue solid;
  }
  .menu_item.active {
    border: 1px green solid;
  }
   
  .scroll_menu_arrow {
    padding: 20px;
    cursor: pointer;
  }
  .calendarSetup{
      display: flex;
      flex-direction: row;
  }
  @media screen and (max-width: 767px) {
    .allBtn {
      min-width: 7rem;
    }
    .calendarSetup{
        display: none;
    }
    .flexEnd{
        position: fixed;
        right: 0;
        bottom: 0;
        z-index: 100;
        margin-bottom: 20px;
        margin-right: 20px;
    }
  }
  