.register {
  padding: 20px 0px;
}

.register_wrapper {
  display: block;
  position: relative;
  border: 3px solid #439a86;
  padding: 15px;
  border-radius: 10px;
  background-color: white;
}

.register_image {
  box-sizing: border-box;
  width: 100%;
  max-width: 120px;
  height: auto;
  margin: 10px;
}

.register_header {
  padding: 10px 0px 50px 0px;
}

.register_submit {
  background-color: #439a86;
  color: white;
  padding: 10px;
  width: 300px;
  border: 0px;
  font-size: 16px;
  border-radius: 3px;
  margin-top: 30px;
  margin-bottom: 30px;
}

.register_submit:hover {
  background-color: #17866c;
}

.back_to_home {
  text-decoration: none;
  text-align: center;
  font-size: 12px;
  color: #439a86;
}

.back_to_home:hover {
  text-decoration: none;
  text-align: center;
  font-size: 12px;
  font-weight: bold;
  color: #439a86;
}

.input_group {
  border-radius: 5px !important;
}

.input_group_text {
  border-radius: 5px 0px 0px 5px;
  border: 1px solid #439a86;
  background-color: #439a86;
  width: 150px;
  color: white;
}

.form_control {
  border: 1px solid #439a86;
}

.register_submit {
  background-color: #439a86;
  color: white;
  padding: 10px;
  width: 300px;
  border: 0px;
  font-size: 16px;
  border-radius: 3px;
  margin-top: 30px;
  margin-bottom: 30px;
}

@media (min-width: 800px) {
  .register {
    padding: 50px 0px;
  }

  .register_wrapper {
    padding: 25px 100px;
  }
}
